<template>
  <v-layout wrap align-center v-if="launches">
    <v-flex xs12 sm6 md4 class="pa-2" v-for="(launch, id) in launches" :key="id">
      <v-card>
        <v-card-title class="justify-center">
          <h3 class="headline">{{ launch.name }}</h3>
        </v-card-title>
        <v-list light>
          <v-list-tile avatar>
            <v-avatar>
              <v-icon>access_time</v-icon>
            </v-avatar>
            <v-list-tile-content>
              <v-list-tile-title>
                <v-tooltip top>
                  <v-badge color="red" slot="activator">
                    <span slot="badge" v-if="launch.tbddate === 1 || launch.tbdtime === 1">!</span>
                    {{ new Date(launch.net).toLocaleString() }}
                  </v-badge>
                  <span v-if="launch.tbddate === 1 || launch.tbdtime === 1">Launch date is not exact</span>
                  <span v-else>Launch date</span>
                </v-tooltip>
              </v-list-tile-title>
              <v-list-tile-sub-title>Launch Date</v-list-tile-sub-title>
            </v-list-tile-content>
          </v-list-tile>
        </v-list>
        <v-card-actions>
          <v-btn large color="primary" @click.stop="getLaunchDetails(launch.id)">Details</v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
    <DetailsModal :dialog="detailsDialog" :closeDialog="closeDetailsDialog" :mutateDialog="mutateDetailsDialog" :launch="launchClicked" />
  </v-layout>
</template>
<script>
import DetailsModal from './DetailsModal'

export default {
  data () {
    return {
      launches: null,
      agencyId: this.id,
      agencyName: this.name,
      detailsDialog: false,
      launchClicked: null
    }
  },
  props: {
    id: {
      type: [String, Number]
    },
    name: {
      type: String
    }
  },
  created () {
    if (this.$store.getters.agencyUpcomingLaunches(this.agencyId)) {
      this.launches = [...this.$store.getters.agencyUpcomingLaunches(this.agencyId)]
    } else {
      this.$store.dispatch('getAgencyUpcomingLaunches', this.agencyId)
        .then(() => {
          this.launches = [...this.$store.getters.agencyUpcomingLaunches(this.agencyId)]
        })
        .catch(error => {
          console.log(error)
        })
    }
  },
  methods: {
    getLaunchDetails (id) {
      if (this.$store.state.launchDetails && this.$store.state.launchDetails[id]) {
        this.launchClicked = { ...this.$store.state.launchDetails[id] }
        this.detailsDialog = true
      } else {
        this.$store.dispatch('getLaunchDetails', id)
          .then(response => {
            this.launchClicked = { ...this.$store.state.launchDetails[id] }
            this.detailsDialog = true
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    closeDetailsDialog () {
      this.detailsDialog = false
    },
    mutateDetailsDialog (newValue) {
      this.detailsDialog = newValue
    }
  },
  components: {
    DetailsModal
  }
}
</script>



