import Vue from 'vue'
import Router from 'vue-router'
import Start from '@/components/Start'
import PastLaunches from '@/components/PastLaunches'
import UpcomingLaunches from '@/components/UpcomingLaunches'
import Agencies from '@/components/Agencies'
import LaunchModal from '@/components/LaunchModal'
import SpaceXPast from '@/components/SpaceXPast'
import SpaceXUpcoming from '@/components/SpaceXUpcoming'

Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Start',
      component: Start,
      children: [
        {
          path: '/past/spacex',
          name: 'SpaceXPast',
          component: SpaceXPast,
          children: [
            {
              path: '/past/spacex/:name',
              name: 'LaunchModal',
              component: LaunchModal
            }
          ]
        },
        {
          path: '/upcoming/spacex',
          name: 'SpaceXUpcoming',
          component: SpaceXUpcoming
        },
        {
          path: '/agencies',
          name: 'Agencies',
          component: Agencies
        },
        {
          path: '/agencies/past/:id',
          name: 'AgencyPastLaunches',
          component: PastLaunches,
          props: true
        },
        {
          path: '/agencies/upcoming/:id',
          name: 'AgencyUpcomingLaunches',
          component: UpcomingLaunches,
          props: true
        }
      ]
    }
  ]
})

export default router



