<script>
import { Pie } from 'vue-chartjs'

export default {
  extends: Pie,
  data () {
    return {
      data: {
        labels: [this.agencyAbbrev, 'Others'],
        datasets: [
          {
            backgroundColor: ['#41B883', '#00C3E6'],
            data: this.amount
          }
        ]
      },
      options: {
        responsive: true,
        maintainAspectRatio: false,
        legend: {
          labels: {
            fontSize: 16,
            generateLabels: () => {
              return [{
                text: `${this.agencyAbbrev}: ${this.amount ? this.amount[0] : 0}`,
                fillStyle: '#41B883'
              }, {
                text: `Others: ${this.amount ? this.amount[1] : 0}`,
                fillStyle: '#00C3E6'
              }]
            }
          }
        }
      }
    }
  },
  props: {
    amount: {
      type: Array
    },
    agencyAbbrev: {
      type: String
    }
  },
  mounted () {
    this.renderChart(this.data, this.options)
  }
}
</script>



