<template>
  <div>
    <h2 class="headline pb-4">{{ agencyName }}</h2>
    <Chart v-if="launchesByYears" style="min-height: 70vh" :openDialog="openDialog" :launches="launchesByYears" :agencyAbbrev="agencyAbbrev" />
    <LaunchModal :closeDialog="closeDialog" :dialog="dialog" :launches="launchesByYearClicked" :year="year" :agencyName="agencyName" />
  </div>
</template>
<script>
import Chart from './Chart'
import PieChart from './PieChart'
import LaunchModal from './LaunchModal'

export default {
  data () {
    return {
      agencyId: +this.id,
      agencyAbbrev: this.abbrev,
      agencyName: this.name,
      launches: [],
      launchesByYears: null,
      year: null,
      dialog: false,
      launchesByYearClicked: null
    }
  },
  props: {
    id: {
      type: [Number, String]
    },
    abbrev: {
      type: String
    },
    name: {
      type: String
    }
  },
  created () {
    if (!this.agencyAbbrev || !this.agencyName) {
      this.getAgencyInfo()
    }
    this.getLaunchesByYears()
  },
  methods: {
    getAgencyInfo () {
      if (this.$store.state.agencies) {
        this.agencyAbbrev = this.$store.getters.agencyInfo(this.agencyId).abbrev
        this.agencyName = this.$store.getters.agencyInfo(this.agencyId).name
      } else {
        this.$store.dispatch('getAgenciesInfo')
          .then(() => {
            this.agencyAbbrev = this.$store.getters.agencyInfo(this.agencyId).abbrev
            this.agencyName = this.$store.getters.agencyInfo(this.agencyId).name
          })
          .catch(error => {
            console.log(error)
          })
      }
    },
    getLaunchesByYears () {
      const getAgencyLaunches = new Promise((resolve, reject) => {
        if (this.$store.getters.agencyPastLaunches(this.agencyId)) {
          this.launches = [...this.$store.getters.agencyPastLaunches(this.agencyId)]
          resolve()
        } else {
          this.$store.dispatch('getAgencyPastLaunches', this.agencyId)
            .then(() => {
              this.launches = [...this.$store.getters.agencyPastLaunches(this.agencyId)]
              resolve()
            })
            .catch(error => {
              resolve()
              console.log(error)
            })
        }
      })
      const getHistory = new Promise((resolve, reject) => {
        if (this.$store.state.history) {
          resolve(this.$store.state.history)
        } else {
          this.$store.dispatch('getHistory')
            .then(() => {
              resolve(this.$store.state.history)
            })
            .catch(error => {
              console.log(error.msg)
            })
        }
      })
      Promise.all([getAgencyLaunches, getHistory])
        .then(values => {
          this.launchesByYears = {}
          values[1].map(item => {
            const countByYears = this.calcCountByYear(item.year)
            this.launchesByYears[item.year] = { agency: countByYears, total: item.amount }
          })
        })
        .catch(error => {
          console.log(error)
        })
    },
    calcCountByYear (year) {
      return this.launches.length > 0 ? this.launches.filter(item => {
        return new Date(item.net).getFullYear() === year && new Date(item.net).getTime() < Date.now()
      }).length : 0
    },
    openDialog (year) {
      this.launchesByYearClicked = this.launches.filter(item => {
        if (new Date(item.net).getFullYear() === year && new Date(item.net).getTime() < Date.now()) {
          return item
        }
      })
      this.year = year
      this.dialog = true
    },
    closeDialog () {
      this.dialog = false
    }
  },
  components: {
    Chart,
    PieChart,
    LaunchModal
  }
}
</script>



