<template>
  <div>
    <v-card class="pa-4 mx-3">
      <h1 dark class="display-2">SpaceX</h1>
    </v-card>
    <v-list light three-line v-if="info">
      <v-container fluid grid-list-lg>
        <v-layout row wrap justify-center>
          <v-flex xs12 sm6 md4 xl3>
            <v-card>
              <v-list-tile avatar>
                <v-avatar>
                  <v-icon>location_city</v-icon>
                </v-avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="title">{{ `${info.headquarters.state}, ${info.headquarters.city }, ${info.headquarters.address }` }}</v-list-tile-title>
                  <v-list-tile-sub-title>Address</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-card>
          </v-flex>
          <v-flex xs12 sm6 md4 xl3>
            <v-card>
              <v-list-tile avatar>
                <v-avatar>
                  <v-icon>face</v-icon>
                </v-avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="title">{{ info.founder }}</v-list-tile-title>
                  <v-list-tile-sub-title>Founder & CEO</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-card>
          </v-flex>
          <v-flex xs12 sm6 md4 xl3>
            <v-card>
              <v-list-tile avatar>
                <v-avatar>
                  <v-icon>account_circle</v-icon>
                </v-avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="title">{{ info.coo }}</v-list-tile-title>
                  <v-list-tile-sub-title>COO</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-card>
          </v-flex>
          <v-flex xs12 sm6 md4 xl3>
            <v-card>
              <v-list-tile avatar>
                <v-avatar>
                  <v-icon>date_range</v-icon>
                </v-avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="title">{{ info.founded }}</v-list-tile-title>
                  <v-list-tile-sub-title>Founded</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-card>
          </v-flex>
          <v-flex xs12 sm6 md4 xl3>
            <v-card>
              <v-list-tile avatar>
                <v-avatar>
                  <v-icon>people</v-icon>
                </v-avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="title">{{ info.employees }}</v-list-tile-title>
                  <v-list-tile-sub-title>Employees</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-card>
          </v-flex>
          <v-flex xs12 sm6 md4 xl3>
            <v-card>
              <v-list-tile avatar>
                <v-avatar>
                  <v-icon>attach_money</v-icon>
                </v-avatar>
                <v-list-tile-content>
                  <v-list-tile-title class="title">${{ info.valuation.toLocaleString() }}</v-list-tile-title>
                  <v-list-tile-sub-title>Valuation</v-list-tile-sub-title>
                </v-list-tile-content>
              </v-list-tile>
            </v-card>
          </v-flex>
          <v-flex xs12>
            <v-card>
              <p class="headline pa-4">{{ info.summary }}</p>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-list>
  </div>
</template>
<script>
export default {
  props: {
    info: {
      type: Object
    }
  }
}
</script>



